.team-reflection-container {
    padding: 20px;
    font-family: Arial, sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.team-reflection-header {
    font-size: 2rem;
    color: var(--primary-color);
    margin-bottom: 20px;
}

.video-section {
    margin-bottom: 40px;
}

.video-title {
    font-size: 1.5rem;
    color: var(--secondary-color);
    margin-bottom: 10px;
}

.video-container iframe {
    width: 100%;
    max-width: 560px;
    height: 315px;
    border: none;
}

.video-description {
    font-size: 1rem;
    color: var(--primary-color);
    margin-top: 10px;
}

.reflection-section {
    background-color: #f8f9fa;
    padding: 20px;
    border-radius: 12px;
    margin-top: 20px;
    text-align: left;
    max-width: 720px;
    margin-left: auto;
    margin-right: auto;
}

.reflection-heading {
    font-size: 1.3rem;
    color: var(--secondary-color);
    margin-bottom: 12px;
}

.reflection-list {
    list-style-type: disc;
    padding-left: 20px;
}

.reflection-list li {
    margin-bottom: 10px;
    line-height: 1.5;
    color: #333;
}