.team-contract-container {
    background-color: var(--light-bg-color);
    padding: 20px;
    border-radius: 10px;
    max-width: 1200px;
    margin: 20px auto;
}

.header {
    text-align: center;
    font-size: 2.5rem;
    color: var(--primary-color);
    margin-bottom: 20px;
}

.description {
    font-size: 1.2rem;
    color: var(--primary-color);
    text-align: center;
    margin-bottom: 30px;
}

.pdf-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
}

embed {
    width: 100%;
    height: 80vh;
    border-radius: 8px;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
}

@media (max-width: 768px) {
    .pdf-container embed {
        height: 60vh;
    }
}

.pdf-container embed {
    max-height: 800px;
}